.table-plate{
  margin-top: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 8px;
  background-color: rgba(255,255,255,0.3);
  transition: 100ms linear;
}
.scroll-content{
  min-width: 200px;
  margin: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 100ms linear;
}
